import Vue from 'vue'
import Router from 'vue-router'
const Search = () => import('@/pages/Search/Search.vue')
const About = () => import('@/pages/About/About.vue')

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Search',
      component: Search,
      meta: {title: 'Searching for something'}
    },
    {
      path: '/about',
      name: 'About',
      component: About,
      meta: {title: 'About what you did'}
    }
  ]
})



